// Primary //
$DarkBlueIntro: hsl(217, 28%, 15%); // (intro and email sign up background) //
$DarkBlueMain: hsl(218, 28%, 13%); // main background //
$DarkBlueFooter: hsl(216, 53%, 9%); // footer background //
$DarkBlueTestimonials: hsl(219, 30%, 18%); // testimonials background //

// Accent //
$Cyan: hsl(176, 68%, 64%); // (inside call-to-action gradient) //
$CyanHover: hsl(186, 61%, 72%);
$Blue: hsl(198, 60%, 50%);  // inside call-to-action gradient) //
$LightRed: hsl(0, 100%, 63%); // (error) //

// Neutral //
$White: hsl(0, 0%, 100%);

// Fonts //
$OpenSans: 'Open Sans', sans-serif; // Headings, Call-to-actions, Header Navigation
$Raleway: 'Raleway', sans-serif;